.card {
  background: #fff;
  border-radius: 2px;
  height: 300px;
  margin: 1rem;
  margin-top: 4rem;
  position: relative;
  width: 300px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .cardImage {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .cardImage > img {
  height: 80%;
  width: 80%
}
.card:hover {
  box-shadow: 8px 8px 16px rgb(151, 6, 6);
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


.wrapper {
  -webkit-align-content: flex-start;
          align-content: flex-start;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  height: 100%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  overflow: auto;
  padding-top: 75px;
  padding-bottom: 50px;
}

.navbar-brand {
  white-space: pre
}

#counters {
  color: white
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

